/* header.css */
.top-header {
  background-color: #523737;
  color: white;
  padding: 5px 10px 15px 10px;
  position: fixed;
  width: 100%;
  top: 0;
  z-index: 2;
}

.top-header a {
  color: white;
}

.top-header .header__top,
.top-header .header__bottom {
  display: flex;
  justify-content: space-between;
}

.top-header .header__top {
  margin-bottom: 15px;
  font-size: 12px;
}

.top-header .header__bottom {
  margin: 0 10px;
}

.top-header .header__column {
  width: 33%;
}

.top-header .header__bottom .header__column:first-child {
  color: rgba(255, 255, 255, 0.5);
  cursor: pointer;
}

.top-header .header__column:last-child {
  text-align: right;
}

.top-header .header__column:nth-child(2) {
  text-align: center;
}

.top-header .header__number {
  color: yellow;
}
/* navigation.css */
.tab-bar {
  background-color: #f7f7f7;
  position: fixed;
  width: 100%;
  bottom: 0;
  display: flex;
  justify-content: space-between;
  border-top: 1px solid #c1c1c1;
  padding: 10px 30px 5px 30px;
}

.tab-bar .tab-bar__tab {
  display: flex;
  align-items: center;
  flex-direction: column;
  text-decoration: none;
  color: #c1c1c1;
  transition: transform .3s linear;
}

.tab-bar .tab-bar__tab:hover {
  transform: scale(1.1);
}

.tab-bar .tab-bar__tab i {
  display: block;
  margin-bottom: 5px;
  text-align: center;
  font-size: 24px;
}

.tab-bar .tab-bar__title {
  font-size: 10px;
}

.tab-bar .tab-bar__tab--selected {
  color: #523737;
}
/* search-bar.css */
.search-bar {
  padding: 15px 25px;
  position: relative;
}

.search-bar input {
  width: 100%;
  padding: 5px 7px;
  box-sizing: border-box;
  border: 0;
  border: 1px solid transparent;
  border-radius: 5px;
  background-color: #f1f2f3;
  font-size: 13px;
  text-align: center;
  transition: border-color .2s linear;
}

.search-bar input:focus {
  outline: none;
  border-color: #ffe94a;
}

.search-bar i {
  position: absolute;
  top: 20px;
  left: 40px;
  color: #bbbbbb;
}
/* friends.css */
.friends__section .friends__section-header {
  border-top: 1px solid #ebebeb;
  border-bottom: 1px solid #ebebeb;
  padding: 10px 15px;
  font-size: 12px;
  color: #b4b4b4;
}

.friends__section-rows .friends__section-row {
  padding: 10px 15px;
  display: flex;
  align-items: center;
  font-size: 14px;
}

.friends__section-row.with-tagline {
  justify-content: space-between;
}

.friends__section-tagline {
  font-size: 10px;
  background: #f0f0f0;
  padding: 10px 10px;
  border-radius: 5px;
  color: #939393;
}

.friends__section-row img {
  width: 40px;
  border-radius: 50%;
  margin-right: 15px;
  /*animation: 1s rotateFriend infinite ease-in-out;*/
}

@-webkit-keyframes rotateFriend {
  0% {
    transform: rotateY(0deg);
  }
  50% {
    transform: rotateY(180deg) scale(0.5);
  }
  100% {
    transform: rotateY(360deg);
  }
}

@keyframes rotateFriend {
  0% {
    transform: rotateY(0deg);
  }
  50% {
    transform: rotateY(180deg) scale(0.5);
  }
  100% {
    transform: rotateY(360deg);
  }
}

.friends__section-row a {
  text-decoration: none;
  color: inherit;
}

.friends__section-column {
  display: flex;
  align-items: center;
}
/* chats.css */
.chats .chats__list {
  border-top: 1px solid #ebebeb;
  border-bottom: 1px solid #ebebeb;
}

.chats__list .chats__chat {
  padding: 15px;
  border-bottom: 1px solid #ebebeb;
}

.chats__list .chats__chat:last-child {
  border: none;
}

.chats__chat .chat__content img {
  width: 50px;
  border-radius: 50%;
  margin-right: 15px;
}

.chats__list .chats__chat a {
  display: flex;
  justify-content: space-between;
  text-decoration: none;
  color: inherit;
}

.chats__chat .chat__content {
  display: flex;
  align-items: flex-start;
}

.chat__preview .chat__user {
  font-weight: 600;
  margin-bottom: 5px;
}

.chat__preview .chat__last-message {
  color: #b4b4b4;
  font-size: 11px;
}

.chats__chat .chat__date-time {
  font-size: 10px;
  color: #b4b4b4;
}

.chat-btn {
  width: 60px;
  height: 60px;
  background-color: #ffe94a;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  position: fixed;
  right: 20px;
  bottom: 80px;
  font-size: 20px;
  color: #523737;
  box-shadow: 0px 3px 10px rgba(0, 0, 0, 0.2);
  cursor: pointer;
  transition: color .3s, background .3s/*, transform .5s ease-in-out*/;
}

.chat-btn:hover {
  color: white;
  background: #523737;
  transform: rotateY(1turn);
}
/* find.css */
.find .find__options {
  background-color: #eeeeee;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  padding-top: 25px;
  padding-bottom: 15px;
}

.find__options .find__option {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  padding-left: 30px;
  padding-right: 5px;
  color: #523737;
}

.find__option i {
  margin-bottom: 15px;
}

.find__option span {
  font-size: 12px;
}

.find__recommended header {
  border-bottom: 1px solid #ebebeb;
  padding: 5px 10px;
  font-size: 12px;
  color: #b4b4b4;
}

.recommended__none {
  padding: 20px 0;
  text-align: center;
  border-bottom: 1px solid #ebebeb;
  color: #b4b4b4;
  font-size: 14px;
}
/* more.css */
.more {
  background-color: #f2f2f2;
}

.more .more__header {
  background-color: white;
  padding: 15px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
}

.more__header i {
  color: #523737;
}

.more__header .more-header__column {
  display: flex;
  align-items: center;
}

.more-header__column img {
  width: 50px;
  border-radius: 50%;
  margin-right: 20px;
}

.more-header__info .more-header__title {
  font-weight: 600;
  margin-bottom: 5px;
}

.more-header__info .more-header__subtitle {
  font-size: 12px;
  color: #b4b4b4;
}

.more .more__options,
.more .more__plus-friends {
  margin-bottom: 10px;
  background-color: white;
}

.more .more__options {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.more__options .more__option {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 15px;
  padding-top: 20px;
  color: #523737;
}

.more__option .more__option-title {
  margin-top: 10px;
  font-size: 12px;
}

.more .more__plus-friends {
  padding: 20px;
}

.more__plus-friends .plus-friends__header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 30px;
}

.plus-friends__header .plus-friends__learn-more {
  color: #b4b4b4;
}

.plus-friends__items {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.plus-friends__items .plus-friends__item {
  width: 50%;
  margin-bottom: 30px;
}

.plus-friends__item i {
  margin-right: 20px;
}

.plus-friends__item .plus-friends__item-title {
  font-size: 13px;
}

.more__links {
  display: flex;
  background-color: white;
  padding: 20px;
  margin-bottom: 20px;
}

.more__links .more__option {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-right: 20px;
}

.more__links .more__option img {
  height: 45px;
  margin-bottom: 10px;
}

.more__links .more__option .more__options-title {
  font-size: 12px;
}
/* profile.css */
.top-header--transparent {
  background-color: transparent;
}

.profile .profile__header {
  height: 65vh;
  background-position: center center;
  background-size: cover;
  width: 100%;
  position: relative;
  top: -60px;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
}

.profile__header .profile__header-container {
  text-align: center;
  position: relative;
  bottom: -60px;
}

.profile__header-container img {
  width: 100px;
  border-radius: 50%;
  margin-bottom: 10px;
}

.profile .profile__container {
  margin-top: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.profile__container input {
  padding: 5px;
  margin-bottom: 20px;
}

.profile__actions {
  display: flex;
}

.profile__action {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-right: 15px;
  cursor: pointer;
}

.profile__action-circle {
  background: #f2f2f2;
  padding: 15px;
  border-radius: 50%;
  color: #523737;
  margin-bottom: 15px;
}

.profile__action-title {
  font-size: 12px;
  font-weight: 600;
}
/* chat.css */
.body-chat {
  padding-bottom: 0;
}
.chat-header,
.chat-header a {
  background-color: #a1c0d6;
  color: black;
}

.chat-header {
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}

.chat {
  background-color: #a1c0d6;
  height: 90vh;
  padding-top: 15px;
  padding-left: 5px;
  padding-right: 5px;
}

.chat .date-divider {
  text-align: center;
  font-size: 12px;
  color: rgba(0, 0, 0, 0.5);
  margin-bottom: 15px;
}

.chat__message {
  margin-bottom: 10px;
  display: flex;
  align-items: flex-end;
}

.chat__message-from-me {
  justify-content: flex-end;
  text-align:right;
}

.chat__message--to-me img {
  align-self: flex-start;
}

.chat__message-time {
  font-size: 10px;
  color: rgba(0, 0, 0, 0.5);
}

.chat__message-from-me .chat__message-body {
  background-color: #ffe934;
  margin-left: 5px;
  align-self:flex-end;
}

.chat__message-body {
 align-self:flex-start;
  padding: 10px 5px;
  border-radius: 5px;
  background-color: #ffffff;
  margin-right: 5px;
  margin-top:5px;
  margin-bottom:5px;
}
.left{text-align:left;}
.chat__message--to-me img {
  height: 35px;
  border-radius: 50%;
  margin-right: 10px;
}

.chat__message .chat__message-username {
  font-size: 12px;
  font-weight: 600;
  margin-bottom: 5px;
}

.chat__message-center {
  display: flex;
  flex-direction: column;
}
.chat__message-center span{
	
}
.type-message {
  width: 100%;
  bottom: 0;
  position: fixed;
  height: 45px;
  background: #eeeeee;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-left: 10px;
  padding-right: 5px;
}

.type-message .fa-plus {
  color: #b4b4b4;
}

.type-message__input {
  width: 80%;
  display: flex;
  align-items: center;
  position: relative;
}

.type-message__input input {
  width: 100%;
  padding: 10px;
}

.type-message__input .fa-smile-o {
  position: absolute;
  right: 40px;
  color: #b4b4b4;
}

.type-message__input .record-message {
  color: #523737;
  background-color: #ffe934;
  padding: 10px;
  cursor: pointer;
}
/* bigScreen.css */
.bigScreenText {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-color: #ffe94a;
  z-index: 3;
  color: white;
  font-weight: 600;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 30px;
  text-align: center;
  display: none;
}
/* mobile.css */
@media screen and (min-width: 530px) {
  .bigScreenText {
    display: flex;
  }
}
